<template>
  <body>
    <div id="backgroundTriangle"></div>
    <HeaderLogo />
    <Notification />
    <header id="headerLeft">
      <Navigation />
      <UserCard />
    </header>
    <main id="mainUsersSection">
      <section id="userListSection">
        <h1 class="titles">Użytkownicy</h1>
        <div id="userListContainer">
          <ul id="userListScroll">
            <li
              class="userListElement pointer"
              v-for="user in users"
              :key="user._id"
              @click="activeUser = user._id"
            >
              <img
                class="userAvatar staticImg"
                src="../assets/userAvatar.png"
                alt="User Avatar"
              />
              <h1 class="userNickname">{{ user.nick }}</h1>
              <div class="userMainRole">
                <div class="roleBadge" :class="user.roles[0]">
                  {{ user.roles[0] }}
                </div>
              </div>
            </li>
          </ul>
        </div>
      </section>
      <section id="userDetailsSection">
        <h1 class="titles">Profil</h1>
        <div class="userDetailsContainer" v-for="user in users" :key="user._id">
          <div class="userDetailsVisible" v-if="activeUser === user._id">
            <div class="userOverview">
              <div class="userOverviewDetails">
                <div>
                  <img
                    class="staticImg"
                    src="../assets/userAvatar.png"
                    alt="User Avatar"
                  />
                  <h1>{{ user.nick }}</h1>
                </div>
                <span>{{
                  `Konto utworzone: ${convertDate(user.createdAt)}`
                }}</span>
              </div>
            </div>

            <div class="userRoles">
              <h1 class="userRolesTitle">Rangi użytkownika</h1>
              <div class="userRolesContainer">
                <div
                  class="roleBadge pointer"
                  v-for="role in user.roles"
                  :key="role"
                  :class="role"
                >
                  {{ role }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  </body>
</template>

<script>
import API from "../api";

import HeaderLogo from "../components/HeaderLogo.vue";
import Notification from "../components/Assets/Notification.vue";
import UserCard from "../components/Assets/UserCard.vue";
import Navigation from "../components/Dashboard/MainNavigation.vue";

export default {
  name: "UsersView",
  components: {
    HeaderLogo,
    Notification,
    UserCard,
    Navigation,
  },
  data() {
    return {
      activeUser: 0,
      users: [],
    };
  },
  methods: {
    convertDate(dateToConvert) {
      const date = new Date(dateToConvert);

      let dateDay = date.getDate();
      if (dateDay < 10) dateDay = `0${dateDay}`;
      let dateMonth = date.getMonth() + 1;
      if (dateMonth < 10) dateMonth = `0${dateMonth}`;
      const dateYear = date.getFullYear();

      return `${dateDay}.${dateMonth}.${dateYear}`;
    },
  },
  async beforeMount() {
    this.users = await API.getAllUsers();
  },
};
</script>

<style lang="scss" scoped>
body {
  background-color: #20222a;

  #mainUsersSection {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    width: 100%;
    height: 75%;

    #userListSection {
      width: 30%;

      #userListContainer {
        height: 600px;
        border-radius: 5px;
        padding: 10px;
        background: linear-gradient(to bottom right, #20232a, #1a1c23);
        box-shadow: 0 4px 4px #181920;
        overflow: hidden;

        #userListScroll {
          height: 100%;
          margin: 0;
          padding: 0 15px;
          overflow-y: scroll;

          &::-webkit-scrollbar {
            width: 10px;
          }

          &::-webkit-scrollbar-track {
            background: #282c35;
            border-radius: 5px;
          }

          &::-webkit-scrollbar-thumb {
            background: #105192;
            border-radius: 5px;
          }

          &::-webkit-scrollbar-thumb:hover {
            background: #0e4275;
          }

          .userListElement {
            display: flex;
            flex-flow: row nowrap;
            align-items: center;
            width: 100%;
            height: 75px;
            list-style: none;
            margin: 10px 0;
            padding: 10px;
            border-radius: 5px;
            background: linear-gradient(to bottom right, #23272e, #23272e05);

            &:hover {
              background: linear-gradient(to bottom right, #434a55, #3f455205);
            }

            .userAvatar {
              width: 10%;
              margin-right: 10px;
              border-radius: 50%;
            }

            .userNickname {
              width: 70%;
              text-align: start;
            }

            .userMainRole {
              display: flex;
              justify-content: center;
              width: 20%;
            }
          }
        }
      }
    }

    #userDetailsSection {
      width: 600px;
      height: 600px;

      .userOverview {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        width: 600px;
        height: 100px;
        border-radius: 5px;
        background: linear-gradient(to bottom right, #20232a, #1a1c23);
        box-shadow: 0 4px 4px #181920;

        .userOverviewDetails {
          position: relative;
          top: 25px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 550px;
          height: 60px;
          padding: 0 20px;
          border-radius: 5px;
          background: linear-gradient(to top right, #20232a, #1a1c23);
          box-shadow: 0 4px 4px #181920;

          div {
            display: flex;
            flex-flow: row nowrap;
            align-items: center;
          }

          img {
            width: 40px;
            height: 40px;
            border-radius: 5px;
            margin-right: 10px;
          }

          span {
            background: linear-gradient(to right, #4d4e56, #49505e);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }
      }
      .userRoles {
        margin-top: 100px;

        .userRolesTitle {
          text-align: start;
          margin: 10px 0;
        }

        .userRolesContainer {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          width: 100%;
          height: 50px;
          padding: 0 10px;
          border: 1px solid #282c35;
          border-radius: 5px;
          box-shadow: 0 4px 4px #000;

          .roleBadge:hover {
            text-decoration: line-through;
          }
        }
      }
    }
  }
}
</style>